import React from 'react';
import { Link } from 'react-router-dom';
import './ArticleList.css';

class Articles extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: { list: [], config: {} } };
  }

  componentDidMount() {
    fetch('/article/list.json')
      .then(res => res.json())
      .then(function(data) {
        this.setState({
          data: data
        });
      }.bind(this));
  }

  render() {
    let items = this.state.data.list.map(function(item) {
      let post = this.state.data.config[item];
      let tags = [];
      for (var i in post.tags) tags.push(<span key={post.tags[i]}>&nbsp;<code>{post.tags[i]}</code></span>);
      return (
        <li key={ item }>
          <Link to={ '/post/' + item }>{ post.title }</Link>
          { tags }
        </li>
      );
    }.bind(this));
    return (
      <>
        <p> <b>{ this.state.data.list.length }</b> </p>
        <ul>
          { items }
        </ul>
      </>
    );
  }
}

function ArticleList() {
  return (
    <>
      <h1></h1>
      <Articles/>
    </>
  );
}

export default ArticleList;
