import React from 'react';
import { Link, useParams } from 'react-router-dom';
import './Article.css';
import 'katex/dist/katex.min.css'

const md = require('markdown-it')();
md.use(require('@iktakahiro/markdown-it-katex'));

class ArticleHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: '', id: this.props.id };
  }

  componentDidMount() {
    fetch('/article/list.json')
      .then(res => res.json())
      .then(function(data) {
        this.setState({
          data: data.config[this.props.id].title,
          id: this.props.id
        });
      }.bind(this));
  }

  render() {
    return (
      <>
        <h1>{ this.state.data }</h1>
      </>
    );
  }
}

class ArticleContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: '', id: this.props.id };
  }

  componentDidMount() {
    fetch('/article/source/' + this.props.id + '.md')
      .then(res => res.text())
      .then(function(data) {
        this.setState({
          data: md.render(data),
          id: this.props.id
        });
      }.bind(this));
  }

  render() {
    return (
      <>
        <div dangerouslySetInnerHTML={{ __html: this.state.data }}/>
      </>
    );
  }
}

function Article() {
  let { id } = useParams();
  return (
    <>
      <ArticleHeader id={ id }/>
      <div className="Container">
        <p><Link to="/"></Link>&nbsp;<a href={ '/article/source/' + id + '.md' }></a></p>
        <hr></hr>
        <ArticleContent id={ id }/>
      </div>
    </>
  );
}

export default Article;
